<template>
  <section class="index-container" style="">
    <section class="index-logo">
      <img src="../assets/images/logo.png" alt="this is logo">
    </section>
    <section class="index-title">自由编程协会简历生成软件</section>
    <section class="index-desc">一个模板制作简历的软件平台,让你的简历更加出众 ~</section>
    <section class="index-theme-config">
      <section @click="handleConfigClick(item)" v-for="(item,index) in colors" :key="index" class="theme-config-item" :style="{background:item.color}"></section>
    </section>
    <section class="index-menu-choose">
      <seciton @click="handleMenuChangeClick(item)" v-for="(item,index) in menus" :key="index" class="menu-choose-item">{{item.name}}</seciton>
    </section>
  </section>
  <footer class="index-footer">
    <!-- Copyright @ 2022-2023 All Rights Reserved. Copyright By 郭明 -->
  </footer>
</template>
<script>
import {defineComponent,computed, reactive, toRefs} from "vue";
import router from '../router/index'
export default defineComponent({
  setup(props,context){
    const state=reactive({
        colors:[
          {
            name: 'grg_main_theme',
            color: '#27292d'
          },
          {
            name: 'grg_deepblue_theme',
            color: '#39485c'
          },
          {
            name: 'grg_green_theme',
            color: '#4d705d'
          },
          {
            name: 'grg_purple_theme',
            color: '#545667'
          },
          {
            name: 'grg_accountred_theme',
            color: '#8d5a57'
          },
        ],
        menus: [
          {
            path: '/',
            name: '介绍'
          },
          {
            path: '/resume',
            name: '简历'
          },
          // {
          //   path: '/template',
          //   name: '模板'
          // },
          {
            path: '/sourceCode',
            name: '源码'
          }
        ]
    })
    const handleConfigClick=(item)=>{
      let root=document.querySelector(":root")
      root.style.setProperty("--grg_main_theme",`${item.color}`)
    }
    const handleMenuChangeClick=(item)=>{
      if(item.name=='源码'){
        window.open('https://github.com/the-didi')
        return;
      }
      router.push(item.path)
    }
    return{
      ...toRefs(state),
      handleConfigClick,
      handleMenuChangeClick  
    }
  }
})
</script>
